/* -------------------      ------------------- */
.comRaceTime {
    width: 100%; margin-top: 60px;
    .timeArea {
        width: 100%; padding: 20px 40px; border-radius: 12px; background: #EEF9FD; display: flex; justify-content: space-between; align-items: center;
        .alignL {
            display: flex; justify-content: flex-start; align-items: center;
        }
        .alignR {
            display: flex; justify-content: flex-end; align-items: center;
        }
        .tit {
            color: var(--Gray-900, #111); font-size: 24px; font-style: normal; font-weight: 700; line-height: 1.6;
        }
        .tag {
            width: max-content; height: 26px; margin-left: 8px; padding: 0 10px; border-radius: 20px; 
            color: #FFF; font-size: rem(16px); font-weight: 700; line-height: 1.6;
            &.before {
                background: var(--Secondary); 
            }   
            &.ing {
                background: var(--Point-Orange); 
            }   
            &.end {
                background: #B0B2B7; 
            }           
        }
        .time {
            color: var(--Gray-700); font-size: rem(24px); font-style: normal; font-weight: 500; line-height: 1.4;
        }
    }
}

